import {
  React,
  connect,
  Link,
  ToastNotification,
  localStoragedecryp,
  localStorageencryp,
  Panel,
  PanelHeader,
  Swal,
  setItem,
  PanelBody,
} from "../../../components/helpers/library";
import HeadTransaksiPenjualan from "./HeadTransaksiPenjualan.jsx";
import { setBarangPenjualan } from "../../../actions/penjualan_action.jsx";
import { PageSettings } from "../../../config/page-settings";

class FormTransaksiPenjualan extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "2",
          },
          footer: "Total",
        },

        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },

        // {
        //   dataField: "nama_atribut",
        //   text: "Nama Atribut",
        // },
        // {
        //   dataField: "status_premi",
        //     text: "Premi",
        //     footer:""
        // },

        // {
        //   dataField: "berat_atribut",
        //   text: "Berat Atribut",
        //   formatter: (cell) => {
        //     return <span>{cell.toFixed(3)}</span>;
        //   },
        //   footer: columnData => columnData.reduce((acc, item) => acc + item, 0).toFixed(3)
        // },

        // {
        //   dataField: "harga_atribut",
        //   text: "Harga Atribut",
        //   formatter: (cell) => {
        //     return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
        //   },
        //   footer: columnData => parseInt(columnData.reduce((acc, item) => acc + item, 0)).toLocaleString("kr-KO")

        // },
        {
          dataField: "harga_pergram",
          text: "Harga",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseInt(cell).toLocaleString("kr-KO")}
              </div>
            );
          },
          footer: (columnData) => {
            return (
              <div className="text-right">
                {" "}
                {parseInt(
                  columnData.reduce((acc, item) => acc + item, 0)
                ).toLocaleString("kr-KO")}
              </div>
            );
          },
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseInt(cell).toLocaleString("kr-KO")}
              </div>
            );
          },
          footer: (columnData) => {
            return (
              <div className="text-right">
                {" "}
                {parseInt(
                  columnData.reduce((acc, item) => acc + item, 0)
                ).toLocaleString("kr-KO")}
              </div>
            );
          },
        },

        {
          dataField: "harga_jual",
          text: "Harga Jual",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseInt(cell).toLocaleString("kr-KO")}
              </div>
            );
          },
          footer: (columnData) => {
            return (
              <div className="text-right">
                {" "}
                {parseInt(
                  columnData.reduce((acc, item) => acc + item, 0)
                ).toLocaleString("kr-KO")}
              </div>
            );
          },
        },
        {
          dataField: "berat_jual",
          text: "Berat Jual",
          headerClasses: "text-right",
          formatter: (cell) => {
            return <div className="text-right">{cell.toFixed(3)}</div>;
          },
          footer: (columnData) => {
            return (
              <div className="text-right">
                {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}
              </div>
            );
          },
        },
        {
          dataField: "total",
          text: "Total Harga",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseInt(cell).toLocaleString("kr-KO")}
              </div>
            );
          },
          footer: (columnData) => {
            return (
              <div className="text-right">
                {" "}
                {parseInt(
                  columnData.reduce((acc, item) => acc + item, 0)
                ).toLocaleString("kr-KO")}
              </div>
            );
          },
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index, row)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
  }

  hapusdata(id, row) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.setState({
          status: false,
        });
        let data = JSON.parse(localStoragedecryp("data_barang_penjualan_tmp"));
        data.splice(id, 1);
        localStorageencryp("data_barang_penjualan_tmp", JSON.stringify(data));
        this.props.dispatch(setBarangPenjualan(data));

        let data2 = JSON.parse(localStoragedecryp("data_barang_penjualan_tmp"));

        let cek_pembayaran1 =
          localStoragedecryp("trx_pemabayran_penjualan").length === 0
            ? []
            : JSON.parse(localStoragedecryp("trx_pemabayran_penjualan"));
        // let total_dp = 0;
        // console.log(cek_pembayaran1);
        if (cek_pembayaran1.length !== 0) {
          setItem("trx_pemabayran_penjualan", []);
          let cek_pembayaran = [];
          let total_dp = 0;
          data2.forEach((row) => {
            row.pembayaran.forEach((element) => {
              total_dp = total_dp + element.jumlah_rp;
              let row = {
                jenis: element.jenis,
                keterangan: "TITIPAN",
                nominal: element.jumlah_rp,
              };
              cek_pembayaran.push(row);
              setItem("trx_pemabayran_penjualan", cek_pembayaran);
            });
          });
          setItem("total_dp", total_dp);
        }
        setItem("sisa_bayar_penjualan", []);
        this.setState({
          status: true,
        });
      }
    });
  }

  // cetak() {
  //   let data = JSON.parse(localStorage.getItem("list_component"));
  //   cetakFakturPenjualan(data)
  // }

  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
    localStorage.setItem("data_barang_penjualan_tmp", "[]");
    localStorage.setItem("data_customer_tmp", "[]");
  }
  componentWillUnmount() {
    localStorage.setItem("data_customer_tmp", JSON.stringify([]));
    localStorage.setItem("data_barang_penjualan_tmp", JSON.stringify([]));
    localStorage.removeItem("data_tukar_barang_temp");
  }

  errorhandling(error) {
    ToastNotification("info", "Transaksi Gagal Disimpan");
  }
  render() {
    const version = process.env.REACT_APP_VERSION;
    return (
      <>
        <Link to={version === "full" ? "/SubMenuPenjualan" : "/home"}>
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <div>
          <Panel>
            <PanelHeader>Transaksi Penjualan</PanelHeader>
            <br />
            {/* Transaksi Penjualan */}
            <PanelBody>
              {/* <button onClick={()=>this.cetak()}> Cetak  </button> */}
              {/* {console.log(this.props.simpanBarangTransaksiPenjualan)} */}
              <HeadTransaksiPenjualan
                isLoading={this.state.isLoading}
                columns={this.state.columns}
              />
            </PanelBody>
            <br />
            {/* End Tambah Transaksi Penjualan  */}
          </Panel>
        </div>
      </>
    );
  }
}

export default connect(null)(FormTransaksiPenjualan);
