  getNomorPesanan(nomor_pesanan) {
    this.setState({ isLoadingPesanan: true });
    postData2("pesanan/ambil/no", { no_pesanan: nomor_pesanan.value })
      .then((res) => {
        let databarang = [];
        let row = {
          no_pesanan: res.data.no_pesanan,
          kode_barcode: res.data.kode_barcode,
          nama_barang: res.data.nama_barang,
          nama_atribut: res.data.nama_atribut,
          berat_atribut: res.data.berat_atribut,
          harga_atribut: res.data.harga_atribut,
          harga_jual: res.data.harga_jual,
          harga_pergram: res.data.harga_gram,
          berat_jual: res.data.berat,
          ongkos: res.data.ongkos,
          total:
            parseInt(res.data.harga_jual) +
            parseInt(res.data.harga_atribut) +
            parseInt(res.data.ongkos),
          status_premi: "",
        };
        let hasil = {
          alamat_customer: res.data.pesanan[0].alamat_customer || "-",
          jenis_pelanggan: res.data.pesanan[0].kode_customer || "NONMEMBER",
          kode_customer: res.data.pesanan[0].kode_customer || "NONMEMBER",
          kode_sales: res.data.pesanan[0].kode_sales,
          nama_customer: res.data.pesanan[0].nama_customer || "REGULER",
          no_hp: res.data.pesanan[0].no_hp || "-",
        };
        localStorageencryp("data_customer_tmp", JSON.stringify(hasil));
        databarang.push(row);
        localStorageencryp(
          "data_barang_penjualan_tmp",
          JSON.stringify(databarang)
        );
        this.props.dispatch(setBarangPenjualan(databarang));
        this.setState({ isLoadingPesanan: false });

        let total_dp = 0;
        res.data.pembayaran.forEach((element) => {
          total_dp = total_dp + element.jumlah_rp;
        });

        setItem("total_dp", total_dp);
        this.setState({
          status: "Berhasil",
        });

        this.props.dispatch(hideModal());
      })
      .catch((err) => {
        console.log(err.response);
        localStorageencryp("data_barang_penjualan_tmp", JSON.stringify([]));
        ToastNotification(
          "info",
          err.response === undefined
            ? "Terjadi Kesalahan Saat Mengirim Data "
            : err.response.data.message
        );
        this.setState({ isLoadingPesanan: false });
      });
  }
